.space{
  animation: space-time 24s infinite;
  height: 100vh;
  width: 100%;
  background-color: #03001a;
  position: relative;
  overflow: hidden;

  .star{
    animation: move-space 100s infinite linear;
    width: 2px;
    height: 2px;
    position: absolute;
    border-radius: 100%;
    background-color: #fff;
  }

  .falling-star{
    animation: falling-star 10s reverse;
    position: absolute;
    width: 2px;
    height: 2px;
    border-top-left-radius: 100%;
    border-bottom-left-radius: 100%;
    border-top-right-radius: 1px;
    border-bottom-right-radius: 1px;
    background-color: #fff;
  }
}

@keyframes light-up {
  100%{
    opacity: 1;
  }
}

@keyframes move-space {
  100%{
    transform: translateX(-100vw);
  }
}

@keyframes falling-star {
  10%{
    background-color: #ffcf43;
  }
  50%{
    width: 2px;
    background-color: #fff1e6;
  }
  100%{
    width: 2px;
    top: 0;
    left: 0;
  }
}

@keyframes space-time {
  50%{
    filter: brightness(1.5);
  }
}