@import "./styles/Progress.scss";
@import "./styles/Task.scss";
@import "./styles/EveryDayPlan.scss";
@import "./styles/ModalForm.scss";
@import "./styles/Start.scss";
@import "./styles/Space.scss";

.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

* {
  box-sizing: border-box;
}

body {
  font-family: Inter, Roboto, Oxygen, Fira Sans, Helvetica Neue, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 16px;
  color: #2c3e50;
  background: #2c3e50;
}

hr {
  margin: 1rem 0;
}

strong,
.bold {
  font-weight: 500;
}

a {
  color: #3eaf7c;
  font-weight: 500;
  text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 500;
  line-height: 1.45;
}

h1 {
  font-size: 2.2rem;
  font-weight: 600;
}

h2 {
  font-size: 1.65rem;
  padding-bottom: 0.3rem;
  border-bottom: 1px solid #eaecef;
}

h3 {
  font-size: 1.35rem;
}

mark{
  background-color: transparent;
  font-weight: bold;
}

.btn {
  color: #42b983;
  position: relative;
  place-content: center;
  place-items: center;
  width: fit-content;
  border-radius: 99px;
  letter-spacing: 0.05em;
  border: 1px solid #42b983;
  text-decoration: none;
  text-transform: uppercase;
  margin-right: 10px;
  padding: 0.5rem 1.5rem;
  white-space: nowrap;
  font-weight: 700;
  outline: none;
  background: #fff;
  transition: all 0.22s;

  &:hover {
    cursor: pointer;
    opacity: 0.8;
  }

  &:disabled {
    cursor: not-allowed;
    opacity: 1 !important;
    background: #eee !important;
    border-color: #ddd !important;
    color: #999 !important;
  }

  &:active {
    box-shadow: inset 1px 1px 1px rgba(0, 0, 0, 0.3);
  }

  &-done{
    background-color: transparent;
    user-select: none;
  }
}

.confirm, .delete {
  border-radius: 5px;
  margin: 0 7px;
  padding: 2px 10px;
  font-weight: 900;
  color: #fff;
}

.confirm {
  background-color: #45ef16;
}

.delete {
  background-color: #e53935;
}

.form-control {
  position: relative;
  margin-bottom: 0.5rem;

  input,
  select {
    margin: 0;
    outline: none;
    border: 2px solid #ccc;
    display: block;
    width: 100%;
    color: #2c3e50;
    padding: 0.5rem 1.5rem;
    border-radius: 3px;
    font-size: 1rem;
  }

  small {
    color: #e53935;
  }

  &.invalid input {
    border-color: #e53935;
  }

  &.checkbox input {
    margin-right: 1rem;
  }

  label {
    display: block;
    margin: 0 0 0.3rem 0.3rem;
    font-weight: 500;
  }

  input:active,
  input:focus {
    transition: border 0.22s;
    border: 2px solid #42b983;
  }
}

.hide {
  display: none;
}

.card {
  overflow: hidden;
  padding: 1rem;
  margin-bottom: 1rem;
  border-radius: 10px;
  box-shadow: 2px 3px 10px rgba(0, 0, 0, 0.2);
  background: #fff;
}

.card .input-time {
  margin-top: 10px;
}

.flex {
  display: flex;

  &-space{
    justify-content: space-between;
  }

  .button {
    font-size: 16px;
    padding: 10px 15px;
    border-radius: 5px;
    border: 1px solid #d3fd98;
    cursor: pointer;
    background-color: rgba(229, 255, 187, 0.2);
    transition: .3s;

    &:last-child {
      border-top-left-radius: 0;
      border-bottom-left-radius: 0;
    }

    &:first-child {
      border-top-right-radius: 0;
      border-bottom-right-radius: 0;
    }

    &-active {
      border: 1px solid #d3fd98;
      background-color: #e5ffbb;
    }
  }

}

.list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.lines-tasks {
  display: flex;
  flex-wrap: wrap;
  gap: 2%;

  h3 {
    width: 100%;
  }

  .list-item {
    margin-bottom: 10px;
    padding: 10px;
    width: 32%;
    gap: 10px;
  }
}

.list-item {
  border: 1px solid #fff;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  padding: 0.5rem 0;
  transition: .22s all;
  text-overflow: ellipsis;
  overflow: hidden;
  padding-left: 10px;
  background-color: #fff1c6;
  cursor: pointer;
  &-done {
    background-color: #d0ffc6;
  }
}

.alert p {
  color: #222;
  line-height: 1.7;
}

.navbar-menu li {
  margin-right: 2rem;
}

.navbar-menu li a {
  color: #2c3e50;
  text-decoration: none;
}

.list-item-column {
  flex-direction: column;
  width: 50%;
}

.list-column {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
}

.list-counter {
  width: 100%;
  text-align: right;
}

#my_checkbox[type="checkbox"] {
  position: absolute;
  z-index: -1;
  opacity: 0;
}

.label_checkbox {
  cursor: pointer;
  color: #111;
  font-weight: bold;
}

.important {
  display: flex;
  align-items: center;
  margin-top: 10px;
}

.important input {
  width: 3%;
}

.special_info {
  min-height: 10px;
  color: #f00;
}

.progress-bars {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  gap: 5px;
}