%circle {
  opacity: 0;
  position: relative;
  border: 4px solid #fff;
  border-radius: 100%;
}

%circle-size {
  top: 15%;
  left: 15%;
  width: 70%;
  height: 70%;
}

.start {
  &-loader {
    animation: loader 17s forwards;
    overflow: hidden;
    height: 100vh;
    width: 100%;
    background-color: #000;

    &-clock {
      @extend %circle;
      animation: load-rotate-3D-right-and-scale 12s forwards cubic-bezier(0, 0, .70, 0);
      animation-delay: 4s;

      top: calc(50% - 200px);
      left: calc(50% - 200px);
      width: 400px;
      height: 400px;

      .circle {
        @extend %circle;
        @extend %circle-size;
        animation: load-rotate-3D-left 1s forwards cubic-bezier(0, 0, .60, 0);
        animation-delay: 5s;


        &-lines {
          @extend %circle;
          @extend %circle-size;
          position: absolute;
          animation: start-clock 10s forwards cubic-bezier(0, 0, .70, 0);
          animation-delay: 6.5s;
        }

        .line {
          &-vertical, &-horizontal {
            border-color: #000;
            animation: none;
          }
        }
      }
    }

    .line {
      &-vertical, &-horizontal {
        border: 2px solid #fff;
      }

      &-vertical {
        animation: load-from-top 2s ease;
        top: 0;
        bottom: 0;
        width: 2px;
        left: calc(50% - 2px);
        position: absolute;
      }

      &-horizontal {
        opacity: 0;
        animation: load-from-left 2s cubic-bezier(0, 0, .50, 0) forwards;
        animation-delay: 2s;
        width: 100%;
        height: 2px;
        top: calc(50% - 2px);
        position: absolute;
      }
    }
  }

  &-content{
    position: relative;
    animation: content-load 1s forwards;
    animation-delay: 17s;
    background-color: #fff;
    padding: 1rem;
    border-radius: 10px;
    height: 0;
    overflow: hidden;
  }
}

@keyframes load-from-top {
  0% {
    transform: translate(-49vw, -100%);
  }
  50% {
    transform: translate(-49vw, 0);
  }
}

@keyframes load-from-left {
  0% {
    opacity: 1;
    transform: translate(-100%, -49vh);
  }
  50% {
    transform: translate(0, -49vh);
  }
  100% {
    opacity: 1;
  }
}

@keyframes load-rotate-3D-right-and-scale {
  0% {
    transform: rotate3d(1, 1, 1, 120deg);
  }
  10% {
    transform: rotate3d(1, 1, 1, 0);
    opacity: 1;
  }
  45%{
    transform: scale(1) rotate(0deg);
  }
  80%{
    opacity: 1;
  }
  100%{
    transform: scale(4) rotate(180deg) translateX(-10%);
  }
}

@keyframes load-rotate-3D-left {
  0% {
    transform: rotate3d(1, 1, 1, -120deg)
  }
  100% {
    opacity: 1;
  }
}

@keyframes start-clock {
  0% {
    transform: rotate(0deg);
    opacity: 1;
    z-index: 2;
    background: #000;
    position: absolute;
  }
  100% {
    transform: rotate(5400deg);
    opacity: 1;
    z-index: 2;
    background: #000;
    position: absolute;
  }
}

@keyframes loader {
  90%{
    opacity: 1;
    transform: scale(1);
  }
  99%{
    opacity: 0;
    transform: scale(0.5);
    height: 100vh;
  }
  100%{
    opacity: 0;
    height: 0;
  }
}

@keyframes content-load {
  0%{
    top: -100vh;
  }
  100%{
    height: 100vh;
    top: 0;
  }
}