@import "variables";
//Progress.tsx component styles
.progress {
  .fixed {
    &-hide{
      top: -100px;
      left: 16px;
      right: 16px;
      position: fixed;
      z-index: $modalIndex;
      transition: 0.4s;
    }
    &-show {
      top: 0;
      background-color: #fff;
    }
  }

  .statistic-block {
    margin-bottom: 20px;
  }

  .date-input {
    width: auto;
  }

  .show-done {
    display: flex;
    align-items: center;
    margin-left: 20px;

    span {
      margin-bottom: 2px;
    }

    &-checkbox {
      margin-right: 5px;
      width: auto;
    }
  }

  .list-item {
    transition: 1s;
    animation: absolute-load 0.4s forwards;
  }


  &-bars-today {
    .list-item:not(.list-item-today) {
      animation: absolute-transition 0.4s forwards;
    }
  }

  &-bars-done {
    .list-item-done {
      animation: absolute-transition 0.4s forwards;
    }
  }

  &-bar {
    line-height: 40px;
    margin: 10px 0;
    width: calc(50% - 20px);
  }

  &-line {
    color: transparent;
    cursor: pointer;
    line-height: initial;
    border-radius: 4px;
    width: 100%;
    height: 20px;
    border: 2px solid #3eaf7c;
    display: flex;

    .line {
      transition: 1s;
      height: 100%;
      width: 0;
      background-color: $progressLine;
    }

    .after-line {
      margin-left: -10px;
      height: 100%;
      transition: 0.1s;
      width: 20px;
      background-color: $progressLine;
      border-radius: 100%;
    }
  }
}

@keyframes absolute-load {
  0%{
    opacity: 0;
  }
  100%{
    opacity: 1;
  }
}

@keyframes absolute-transition {
  99%{
    opacity: 0;
    position: initial;
  }
  100%{
    opacity: 0;
    position: absolute;
  }
 }

@keyframes progress-fast {
  50% {
    transform: scaleX(3);
  }
}

@keyframes progress-slow {
  50% {
    transform: scaleX(3);
  }
}

//Save modal styles
.modal {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: fixed !important;
  top: 25%;
  left: 10%;
  width: 80%;
  height: max(50%, 300px);
  background-color: #fff;
  border: 2px solid #282c34;
  padding: 25px 50px;
  border-radius: 20px;
  z-index: $modalIndex;
  transition: 1s;

  &.hide {
    left: -100%;
    display: flex;
    transition: .8s;
  }

  .completed {
    display: flex;
    margin-top: 20px;

    span {
      margin-bottom: 2px;
    }

    &-checkbox {
      margin-right: 5px;
      width: auto;
    }
  }

  .flex {
    justify-content: flex-end;
  }
}